<template>
  <div class="venueIndex">
    <venue-head title="我预约的" down="我预约的" :headList="headList"></venue-head>
    <section class="top-bar">
      <span :class="{cur: !this.past}" @click="reservationTab(false)">待审核 (<b class="red">{{numbers||daishenNumber}}</b>)</span>
      <span class="bar-border" :class="{cur: this.past}" @click="reservationTab(true)">已审核</span>
    </section>
    <venue-lists :totalPages="totalPages" :typleNames='typleNames' :past="past" :pendingLists="dataList" @scroll="scroll" @listSearch="_listSearch"></venue-lists>
    <venue-footer @fatherMethod="fatherMethod"></venue-footer>
  </div>
</template>
<script>
import Vue from 'vue'
import axios from 'axios'
import VenueHead from './public/Head.vue'
import VenueLists from './public/Lists.vue'
import VenueFooter from './public/Footer.vue'
export default {
  name: 'venueIndex',
  data () {
    return {
      headList: [
        {name: '场馆优先', key: 'venuePriority', ico:'venueIco'},
        {name: '时间优先', key: 'timePriority', ico: 'timeIco'},
      ],
      page: 0,
      size: 10,
      numbers: '',
      dataList: {},
      past: false,
      footerBooler: false,
      searchName: '',
      timeTyple: '',
      stateTyple: '',
      determineBooler: false,
      scrollBooler: false,
      typleNames: '预约待审',
      totalPages: false,
      daishenNumber: 0
    }
  },
  components: {
    VenueHead,
    VenueLists,
    VenueFooter
  },
  // 创建前状态
  beforeCreate: function () {
    axios.get('iapp/api/selectedschool')
    .then(res => {
      Vue.prototype.selectedschool = eval(res.data)
    })
  },
  created: function () {
    let boolerts = localStorage.boolerts
    this.daishenNumber = localStorage.daishenNumber
    this.footerBooler = localStorage.footerBooler || false
    if(boolerts !== undefined &&boolerts!=='undefined') {
      this.determineBooler = JSON.parse(localStorage.determineBooler)
      if(JSON.parse(boolerts)){
        this.stateTyple = localStorage.stateTyple
        this.timeTyple = localStorage.timeTyple
        if(JSON.parse(this.footerBooler)||this.determineBooler){
          this.past = JSON.parse(boolerts)
          this.reservationReview()
        }else{
          this.past = JSON.parse(boolerts)
          this.reservationList()
        }
      }else{
        this.timeTyple = localStorage.timeTyple
        this.stateTyple = localStorage.stateTyple
        if(JSON.parse(this.footerBooler)){
          this.reservationReview()
        }else{
          this.reservationList()
        }
      }
    }else{ 
      this.reservationList()
    }
  },
  // 方法声明：用于绑定html中的事件方法
  methods: {
    reservationTab (booler) {
      this.past = booler
      localStorage.timeTyple = ''
      localStorage.stateTyple = ''
      localStorage.boolerts = booler
      localStorage.determineBooler = false
      this.scrollBooler = false
      this.page = 0
      this.totalPages = false
      this.$Toast.showLoading();
      this.dataList = {}
      if(JSON.parse(this.footerBooler)) {
        this.reservationReview()
      }else{
        this.reservationList()
      }
    },
    fatherMethod (booler) {
      this.$Toast.showLoading();
      this.dataList = {}
      this.page = 0
      this.footerBooler = booler
      localStorage.footerBooler = booler
      if(booler) {
        this.past = false
        this.reservationReview()
      }else{
        this.past = booler
        this.reservationList()
      }
    },
    reservationList () {
      let me = this
      let params = {
        params: {
          past: this.past,
          page: this.page,
          size: this.size,
          sort: 'startTime,asc'
        }
      }
      this.past? this.typleNames = "预约已审": this.typleNames = "预约待审"
      localStorage.typleNames = this.typleNames
      let stateTyple = localStorage.stateTyple
      let timeTyple = localStorage.timeTyple
      if(stateTyple && stateTyple !==''&& stateTyple !=="stateAll"){
        params.params.repair_status = stateTyple
      }
      if(timeTyple !=='' && timeTyple !== 'all'){
        params.params.time = timeTyple
      }
      if(this.searchName) {
        params.params.name = this.searchName
      }
      axios.get('iapp/api/app/boardroom/appoint/list', params)
      .then(res => {
        let data = res.data
        me.$Toast.hideToast();
        data.typeName = 'reservation'
        if(data.number+1>=data.totalPages) {
          me.totalPages = true
        }
        data.content.map((i) => {
          i.toFixed = this.toFixed(i.startTime, i.deadLine)
        })
        if(me.scrollBooler) {
          data.content.map( i => {
            me.dataList.content.push(i)
          })
        }else{
          me.dataList = data
        }
        if(!this.past){
          this.numbers = data.totalElements
          localStorage.daishenNumber = data.totalElements
        }
      })
      .catch(error => {
        localStorage.daishenNumber = 0
        me.$Toast.hideToast();
      })
      localStorage.determineBooler = false
    },
    reservationReview () {
      let me = this
      let ID = 257
      let params = {
        params: {
          past: this.past,
          page: this.page,
          size: this.size,
          sort: 'startTime,asc'
        }
      }
      this.past? this.typleNames = "审核已审": this.typleNames = "审核待审"
      localStorage.typleNames = this.typleNames

      if(this.searchName) {
        params.params.name = this.searchName
      }
      if(this.timeTyple !=='' && this.timeTyple !== "all"){
        params.params.time = this.timeTyple
      }
      if(this.stateTyple !=='' && this.stateTyple !== 'stateAll'){
        params.params.repair_status = this.stateTyple
      }
      if(me.selectedschool&&me.selectedschool.id){
        ID = me.selectedschool.id
      }
      axios.get(`iapp/api/app/boardroom/verify/past/${ID}`, params)
      .then(res => {
        let data = res.data
        me.$Toast.hideToast();
        data.content.map( i => {
          i.toFixed = this.toFixed(i.appBoardroomOrder.startTime, i.appBoardroomOrder.deadLine)
        })
        data.typeName = 'review'
        if(data.number+1>=data.totalPages) {
          me.totalPages = true
        }
        if(me.scrollBooler) {
          data.content.map( i => {
            me.dataList.content.push(i)
          })
        }else{
          me.dataList = data
        }
        if(!me.past){
          me.numbers = data.totalElements
          localStorage.daishenNumber = data.totalElements
        }
      })
      .catch(error => {
        console.log(error)
        me.$Toast.hideToast();
      })
    },
    scroll (names) {
      this.scrollBooler = true
      this.typleNames = names
      this.page = this.page+1
      if(names == '审核已审'||names=="审核待审") {
        this.reservationReview()
      }
      if(names == '预约已审'||names=="预约待审") {
        this.reservationList()
      }
    },
    toFixed(startTime, endTime) {
      const start=Date.parse(new Date(startTime.replace(/[-]/g,"/")));
      const end=Date.parse(new Date(endTime.replace(/[-]/g,"/")));
      const time=(end-start)/1000/60/60;
      return time.toFixed(1)+'小时'
    },
    _listSearch (name) {
      this.searchName = name
      if( this.typleNames == "审核已审"||this.typleNames == "审核待审") {
        this.reservationReview()
      }
      if( this.typleNames == "预约已审"||this.typleNames == "预约待审") {
        this.reservationList()
      }
      this.searchName = ''
    }
  },
}
</script>
<style lang="less" scoped>
.venueIndex {
  height: 100%;
  width: 100%;
  background: #f5f7fa;
  .top-bar {
    overflow: hidden;
    background: #fff;
    span {
      width: 50%;
      display: block;
      float: left;
      text-align: center;
      box-sizing: border-box;
      padding: 8px 0;
      border-bottom: 2px solid #fff;
      color: #999;
      .red { color: red }
      &:last-child {
        border-right: none;
        position: relative;
      }
      &.cur {
        color: #219afe;
        border-bottom: 2px solid #219afe;
      }
    }
    .bar-border:before {
      content: "";
      display: block;
      position: absolute;
      width: 1px;
      height: 23px;
      background: #e4e4e4;
      top: 50%;
      margin-top: -11px;
    }
  }
}
</style>



