<template>
  <ul class="footers">
    <li>
      <a href="javascript:;" @click="reviewTaFn(false)" :class="{cur: !this.reviewTaBol}">我预约的</a>
    </li>
    <li>
      <a href="javascript:;" @click="reviewTaFn(true)" :class="{cur: this.reviewTaBol}">我审核的</a>
    </li>
  </ul>
</template>
<script>
export default {
  name: "footers",
  data () {
    return {
      reviewTaBol: false
    }
  },
  created () {
    let typleNames = localStorage.typleNames
    if(typleNames == '审核待审' || typleNames == "审核已审") {
      this.reviewTaBol = true
    }
  },
  methods: {
    reviewTaFn (booler) {
      this.reviewTaBol = booler 
      this.$emit('fatherMethod', booler);
    }
  },
}
</script>
<style lang="less" scoped>
.footers {
  background: #fff;
  border-top: 1px solid #e6e6e6;
  display: flex;
  flex: 1;
  justify-content: space-around;
  align-items: center;
  height: 45px;
  li {
    width: 100%;
    height: 100%;
    text-align: center;
    &:last-child {
      border-left: 1px solid #dbdbdb;
    }
    a {
      color: #333;
      display: block;
      line-height: 2.8125rem /* 45/16 */;
      &.cur {
        color: #219afe;
      }
    }
  }
}
</style>



