<template>
<div class="wrapper" ref="wrapper" :style="{height: clientHeight+'px'}">
  <ul class="lists">
    <li>
      <div class="search">
        <div class="border">
          <div class="weui_cell_bd" @click="_more">
            <i></i>
          </div>
          <i class="right-border"></i>
          <div class="weui_cell_hd">
            <input @change="changeSearch" v-model.lazy="name" class="weui_input" type="text" placeholder="请输入名称">
          </div>
        </div>
      </div>
    </li>
    <li class="errorLi" v-if="pendingLeng">
      <div class="error"></div>
    </li>
    <template v-if="pendingLists&&pendingLists.typeName == 'reservation'">
      <li v-for="(item, index) of pendingLists.content" :key="index">
        <div class="content">
          <router-link :to="{name: 'VenueDetails', params: {item}}">
            <h4>{{item.title}}</h4>
            <p class="info">{{item.createTime|formatTime}}</p>
            <p class="title">预约场馆：{{item.appBoardroomDetail.roomName}}</p>
            <p class="title">使用时长：{{item.toFixed}}</p>
          </router-link>
          </div>
        <div class="footer-tab">
          <router-link :to="{name: 'VenueDetails', params: {item}}">详情</router-link>
          <a href="javascript:;"></a>
          <a href="javascript:;" class="detail-category">
            <span class="schedule"></span>
            <span class="msgIco"></span>
            <span class="forwardIco"></span>
          </a>
        </div>
      </li>
    </template>
    <template v-if="pendingLists&&pendingLists.typeName == 'review'">
      <li v-for="(item, index) of pendingLists.content" :key="index">
        <div class="content">
          <router-link :to="{name: 'VenueDetails', params: {item}}">
            <h4>{{item.appBoardroomOrder.title}}</h4>
            <p class="info">{{item.appBoardroomOrder.createTime|formatTime}}</p>
            <p class="title">预约场馆：{{item.appBoardroomOrder.appBoardroomDetail.roomName}}</p>
            <p class="title">使用时长：{{item.toFixed}}</p>
          </router-link>
        </div>
        <div class="footer-tab">
          <router-link :to="{name: 'VenueDetails', params: {item}}">详情</router-link>
          <a href="javascript:;"></a>
          <a href="javascript:;" class="detail-category">
            <span class="schedule" @click="application('schedule',{item})"></span>
            <span class="msgIco" @click="application('msgIco')"></span>
            <span class="forwardIco" @click="application('forwardIco')"></span>
          </a>
        </div>
      </li>
    </template>
  </ul>
</div>
</template>
<script>
import BScroll from 'better-scroll'
import axios from 'axios'
export default {
  name: "Lists",
  data () {
    return {
      name: '',
      clientHeight: '',
      pendingLeng: false,
      PagesNumber: 0,
    }
  },
  props: {
    pendingLists: Object,
    past: Boolean,
    typleNames: String,
    totalPages: Boolean
  },
  created () {
    this.$nextTick ( () => {
      this.InitTabScroll ();
    });
  },
  methods: {
    application (name, data){
      var Url = '';
      if(name=="schedule"){
          data.path = '/api/service/file/5c05e7cb39572f1e466c4133';
          localStorage.setItem("personnelInf", JSON.stringify(data))
          Url = "/iapp/enterSchedule"
      } else if(name == 'msgIco'){
          Url = ''
      }else if(name == 'forwardIco'){
          Url = ''
      }
      location.href = Url
    },
    _more () {
      this.$router.push({path:'/ConditionalScreening', query: {boolert:this.past}})
    },
    changeSearch (e) {
      this.name = e.target.value
      this.$emit("listSearch", this.name);
      setTimeout(() => {this.name = ''}, 4000)
    },
    InitTabScroll () {
      const me = this
      this.clientHeight = `${document.documentElement.clientHeight}`-130
      if (!this.scroll) {
        this.scroll=new BScroll(this.$refs.wrapper, {
          click: true,
          scrollY: true,
          pullUpLoad: {
            threshold: 50,
            moreTxt: '加载更多',
            noMoreTxt: '没有更多数据了'
          }
        });
      }else{
        this.scroll.refresh()
      }
      //touchEnd（手指离开以后触发） 通过这个方法来监听下拉刷新
      this.scroll.on('touchEnd', (pos) => {
        if(pos.y < -1300 && !me.totalPages){
          let typleNames = localStorage.typleNames
          if(typleNames){
            this.$emit('scroll',typleNames)
          }
        }
      })
    }
  },
  updated: function () {
    this.pendingLeng = this.pendingLists&&this.pendingLists.content&&this.pendingLists.content.length < 1 ? true : false
  },
}
</script>
<style lang="less" scoped>
.wrapper {
  overflow: hidden;
}
.lists {
  li {
    list-style: none;
    overflow: hidden;
    position: relative;
    .search {
      padding: .625rem 1.25rem 0 1.25rem;
      .border {
        display: flex;
        flex: 1;
        border: 1px solid #dbdbdb;
        border-radius: 1.25rem /* 20/16 */;
        background: #fff;
        .right-border {
          display: inline-block;
          margin: .5rem .375rem ;
          border: .51px solid #dbdbdb;
        }
        .weui_cell_bd {
          width: 3.125rem; /* 50/16 */
          text-align: right;
          position: relative;
          i {
            background: url(/static/images/searchIco.png) no-repeat center;
            background-size: 1.125rem /* 18/16 */;
            width: 2.5625rem /* 41/16 */;
            height: 1.3125rem /* 21/16 */;
            position: absolute;
            right: -.5625rem /* 9/16 */;
            top: 32%;
            margin-top: -.375rem /* 6/16 */;
          }
        }
        .weui_cell_hd:after {
          content: " ";
          display: block;
          position: absolute;
          width: 1rem /* 16/16 */;
          height: 1.25rem /* 20/16 */;
          background: url(/iapp/v1.0.01/img/search.png) no-repeat;
          background-size: 1rem /* 16/16 */;
          left: .375rem /* 6/16 */;
          top: 50%;
          margin-top: -.5rem /* 8/16 */;
        }
        .weui_cell_hd {
          width: 100%;
          position: relative;
          background: #fff;
          height: 1.375rem /* 22/16 */;
          line-height: 1.25rem /* 20/16 */;
          padding: .25rem .625rem .25rem 1.75rem;
          margin-right: .625rem;
          border-radius: .1875rem /* 3/16 */;
          .weui_input {
            width: 100%;
            background:#fff
          }
        }
      }
    }
    &:not(:first-child){
      border-bottom: 1px solid #f1f1f1;
      background: #fff;
      margin-top: .75rem; /* 12/16 */
    }
    .footer-tab, .content {
      padding: .625rem 1.25rem;
    }
    .content {
      border-bottom: 1px solid #e6e6e6;
      h4 {
        color: #333
      }
      p {
        margin-top: .625rem;
        color: #666;
      }
      .info {
        color: #b6b6b6;
        line-height: 18px;
      }
    }
    .footer-tab {
      display: flex;
      justify-content: space-between;
      a {
        color: #666;
        
      }
      .detail-category {
        span {
          display: inline-block;
          padding: .625rem /* 10/16 */;
          margin-right: .625rem /* 10/16 */;
        }
        span.schedule {
          background: url(/static/images/scheduleIco.png) no-repeat center;
          background-size: 1.25rem /* 20/16 */;
        }
        span.msgIco {
          background: url(/static/images/msgIco.png) no-repeat center;
          background-size: 1.25rem /* 20/16 */;
        }
        span.forwardIco {
          margin-right: 0;
          background: url(/static/images/forwardIco.png) no-repeat center;
          background-size: 1.25rem /* 20/16 */;
        }
      }
    }
    &.errorLi {
      background: #f5f7fa;
      border: none;
      margin-top: 10rem /* 160/16 */; 
      .error {
        width: 100px;
        height: 100px;
        background: url(/static/images/nulldata.png) no-repeat center;
        background-size: contain;
        margin: 0 auto;
      }
    }
  }
}
</style>



